*{
    font-family: 'Roboto', sans-serif;
    font-weight: 300;
}

.movie {
    margin: 10px;
    /* border: 1px solid black; */
    background-color: white;
    width: 250px;
    font-weight: 300;
    height: 600px;
}

.movie li {
    list-style:none;
}
.imgs{
    width: 185px;
}

.movie__year{
    font-weight: 300;
    color: gray;
    font-size: 15px;
}

.movie ul {
    display: flex;
    padding-left: 0;
    font-weight: 300;
    color: gray;
    font-size: 15px;
}
.movie ul li {
    margin-right: 5px;
}
.movie__title{
    font-weight: 300;
    font-size: 25px;
}
.movie {
